import React from "react";
import "./footer.css";
import { Link } from 'react-router-dom';

function Footer() {
    return (
        <div className="footer">
            <Link to="/cart" className="foot-link">My Basket</Link>
            <a className="foot-link" href="#Goodies">Goodies</a>
            <a className="foot-link" href="#About">About</a>
            <a className="foot-link" href="#Home">Home</a>

            <div>
                <p className="copyright">CopyRight So Special Sweet 2021</p>
                <p className="copyright">All Rights Reserved</p>
            </div>
        </div>
    )
};

export default Footer;