import React from "react";
import "./specials.css";

function Specials(props) {

    function buildMenuSpecials() {
        let menu = props.menus;
        let builtMenu = [];
        menu.forEach((item, i, arr) => {
            if (item.special === true) {
                builtMenu.push(
                    <div key={"menu" + i} className="menu">
                        <div className={"shape cupcake" + i}></div>
                        <div className="cupcake-text">
                            <h2 className="cupcake-header">{item.name}</h2>
                            <p className="cupcake-description">{item.description}</p>
                            <div className="cupcake-pricing-div">
                                <p className="cupcake-price">${item.price}</p>
                                <button className="cupcake-btn" onClick={() => {
                                    props.incrementQ(i, item.quantity)
                                }}>+</button>
                                <p>{item.quantity}</p>
                                {/* <input type="number" min="0"
                                // value={item.quantity} 
                                onChange={(i, e) => { props.updateTotal(i, e) }}></input> */}
                                <button className="cupcake-btn" onClick={() => { props.decrementQ(i, item.quantity) }}>-</button>
                                <div className="order-div">
                                    {/* <div className="basket-img"></div> */}
                                    <button className="basket-btn" onClick={() => { props.cart(i) }}><p>Add to Basket</p></button>
                                </div>
                            </div>
                        </div>
                    </div >)
            }
        }
        )
        return builtMenu;
    }

    return (

        <div id="Specials">
            <div className="special-bg">
                <div className="special-image"> </div>
                <div className="special-text">
                    <div className="special-intro">
                        <h2 className="special-header">St. Patrick's Day Specials</h2>
                        <p className="special-paragraph">Find some Lucky Clover Cupcakes or Pot O' Gold Cookie Bombs</p>
                    </div>
                    <div className="special-products">
                        {buildMenuSpecials()}
                    </div>
                </div>
            </div>
        </div>
    )
};

export default Specials;