import React from "react";
import { BrowserRouter as Router, Route } from 'react-router-dom';

import './App.css';
import Nav from './Components/Nav/Nav';
import Intro from './Components/Intro/Intro';
import Hover from './Components/Hover/Hover';
// import Order from './Components/Order/Order';
import Footer from './Components/Footer/Footer';
import Explanation from './Components/Explanation/Explanation';
import Menu from './Components/Menu/Menu';
import Specials from './Components/Specials/Specials';
import Cart from './Components/Cart/Cart';


class App extends React.Component {

  constructor(props) {
    super(props)
    //set state directly.
    this.state = {
      cart: false,
      forSale: [
        {
          id: 0,
          name: "The Chocolate-Chocolate",
          description: "A delicious chocolate cupcake with rich chocolate frosting. A real crowd favorite.",
          price: 4.00,
          pricegroup: [3, 2.00],
          quantity: 0,
          inCart: false
        },
        {
          id: 1,
          name: "The Sweet Vanilla",
          description: "Safe, sweet and marvelous this vanilla is a safe bet for many picky flavorists.",
          price: 10.40,
          quantity: 0,
          inCart: false
        },
        {
          id: 2,
          name: "Strawberry Punch",
          description: "A fun sweet tangy flavor topped with sliced strawberry in a cute heart, a fun treat for backyard barbeques or pool parties.",
          price: 9.01,
          pricegroup: [4, 1],
          quantity: 0,
          inCart: false
        },
        {
          id: 3,
          name: "Cherry Twist",
          description: "Cherry flavored cupcake made from fresh in-season bing cherries and topped with chocolate deliciousness.",
          price: 6.02,
          quantity: 0,
          inCart: false
        },
        {
          id: 4,
          name: "Chocolate Cheesecake",
          description: "Creamy like cheesecake and rich like chocolate this is sure to be a total hit.",
          price: 5.53,
          quantity: 0,
          inCart: false
        },
        {
          special: false,
          id: 5,
          name: "Cartoon Champion",
          description: "A favorite for the littles, these bright cupcakes have custom white chocolate characters on top.",
          price: 4.44,
          quantity: 0,
          inCart: false
        },
        {
          special: true,
          id: 6,
          name: "Pot-O-Gold",
          description: "Vanilla cupcake with festive green icing and cloud icing with candy pot of gold and rainbow.",
          price: 10.44,
          quantity: 0,
          inCart: false
        },
        {
          special: true,
          id: 7,
          name: "Special Luck",
          description: "Green something with something you should get it! Super tastey and special, will give you good luck.",
          price: 1,
          quantity: 0,
          inCart: false
        }
      ]
    }
  }

  incrementQuantity = (index, currentQuantity) => {
    let forSale = this.state.forSale
    forSale[index].quantity++
    this.setState({ forSale })
  }

  decrementQuantity = (index, currentQuantity) => {
    if (currentQuantity > 0) {
      let forSale = this.state.forSale
      forSale[index].quantity--
      this.setState({ forSale })
    } else return
  }

  addToCart = (index) => {
    let forSale = this.state.forSale
    if (this.state.forSale[index].quantity !== 0) {
      forSale[index].inCart = true
      this.setState({ forSale })
    } else if (this.state.forSale[index].quantity === 0) {
      forSale[index].inCart = false
      this.setState({ forSale })
    }
  }
  updateTotal = (index, event) => {
    let forSale = this.state.forSale
    const value = event.target.value;
    const valueInt = parseFloat(value);

    if (valueInt > 0) {
      forSale[index].quantity = valueInt
      this.setState({ forSale })
    } else {
      forSale[index].quantity = 0
      this.setState({ forSale })
    }
  }

  removeFromCart = (index) => {
    let forSale = this.state.forSale
    forSale[index].inCart = false
    this.setState({ forSale })
  }

  //toggle

  toggleCartOn = () => {
    this.setState({ cart: true })
  }

  toggleCartOff = () => {
    this.setState({ cart: false })
  }

  render() {
    return (
      <div className="App">
        <Router basename={`${process.env.PUBLIC_URL}/`}>
          <Route exact path='/'>
            <Nav />
            <Intro />
            <Specials
              menus={this.state.forSale}
              incrementQ={this.incrementQuantity}
              decrementQ={this.decrementQuantity}
              cart={this.addToCart} />
            <Menu
              menus={this.state.forSale}
              incrementQ={this.incrementQuantity}
              decrementQ={this.decrementQuantity}
              cart={this.addToCart} />
            <Explanation />
            <Hover />
            <Footer />
          </Route>
          <Route exact path='/cart'>
            <Cart
              menus={this.state.forSale}
              updateTotal={this.updateTotal}
              remove={this.removeFromCart} />
          </Route>
        </Router>
      </div>
    )
  }
};

export default App;
