import React from "react";
import "./menu.css";

function Menu(props) {

    function buildMenu() {
        let cakesMenu = props.menus;
        let builtMenu = [];
        function groupCheck(item) {
            if (item.pricegroup !== undefined) {
                return (<span> or {item.pricegroup[0]} for <b>${(item.pricegroup[1] * item.pricegroup[0]).toFixed(2)}</b></span>)
            }
        }
        cakesMenu.forEach((item, i, arr) => {
            if (item.special !== true) {
                builtMenu.push(
                    <div key={"menu" + i} className="menu">
                        <div className={"shape cupcake" + i}></div>
                        <div className="cupcake-text">
                            <h2 className="cupcake-header">{item.name}</h2>
                            <p className="cupcake-description">{item.description}</p>
                            <p className="price"><b>${item.price}</b> each{groupCheck(item)}</p>
                            <div className="cupcake-pricing-div">
                                {/* <p className="cupcake-price">${item.price}</p> */}
                                <button className="cupcake-btn" onClick={() => {
                                    props.incrementQ(i, item.quantity)
                                }}>+</button>
                                <p>{item.quantity}</p>
                                <button className="cupcake-btn" onClick={() => { props.decrementQ(i, item.quantity) }}>-</button>
                                <div className="order-div">
                                    <button className="basket-btn" onClick={() => { props.cart(i) }}><p>Add to Basket</p></button>
                                </div>
                            </div>
                        </div>
                    </div >)
            }
        }
        )
        return builtMenu;
    }

    return (
        <div id="Goodies">
            <div className="favorite-banner"><h1 className="favorite-text-title">Fill Your Goodie Basket</h1></div>
            <div className="main-menu">
                {buildMenu()}
            </div>
        </div>
    )
};

export default Menu;